@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
}

body {
  font-family: 'Montserrat', sans-serif;
}

#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.header {
  padding: 20px 20px;
  background-color: #3695e6;
  display: flex;
  justify-content: space-between; }

.navbar__link {
  color: white;
  text-decoration: none;
  margin-right: 20px; }
  .navbar__link:last-child {
    margin-right: 0; }
  .navbar__link--active {
    text-decoration: underline; }

.logo {
  font-size: 22px;
  font-weight: 500;
  color: white;
  text-decoration: none; }

.main {
  flex-grow: 2; }

.container {
  padding: 0 15px;
  width: 100%; }

@media screen and (min-width: 576px) {
  .container {
    padding: 0;
    margin: 0 auto;
    max-width: 540px; } }

@media screen and (min-width: 768px) {
  .container {
    max-width: 750px; } }

@media screen and (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1140px; } }

.welcome-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0 40px 0; }
  .welcome-block__title {
    font-size: 72px;
    letter-spacing: 2px;
    font-weight: 700;
    color: #b31d1d; }
  .welcome-block__link {
    height: 40px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
    padding: 0 20px;
    border-radius: 4px;
    background-color: #0089ff;
    color: white;
    text-decoration: none;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s; }
    .welcome-block__link:hover {
      background-color: #027ae2; }

.news-list {
  padding: 20px 0 40px 0; }
  .news-list__search {
    height: 35px;
    display: flex;
    margin-bottom: 10px; }
    .news-list__search input {
      height: 100%;
      border-radius: 3px;
      border: 1px solid silver;
      padding: 0 10px; }
  .news-list__row {
    display: flex;
    flex-wrap: wrap; }
  .news-list__col {
    margin-bottom: 20px; }
  .news-list__result {
    margin-bottom: 20px; }
  .news-list__button {
    height: 35px;
    padding: 0 15px;
    margin-left: 20px;
    display: flex;
    align-items: center;
    color: white;
    border-radius: 3px;
    border: none;
    background-color: blue; }

.news-item {
  padding: 5px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  display: flex; }
  .news-item__frame {
    display: flex;
    width: 250px;
    min-width: 250px;
    margin-right: 10px; }
  .news-item__image {
    max-width: 100%; }
  .news-item__content {
    padding-bottom: 10px; }
  .news-item__title {
    margin-top: 0;
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 10px; }
  .news-item__description {
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 10px; }
  .news-item__date {
    color: #868686;
    font-size: 14px; }
  .news-item__bottom {
    display: flex;
    justify-content: space-between; }
  .news-item__link {
    text-decoration: none;
    color: #042767;
    font-weight: 700;
    position: relative; }
    .news-item__link:before {
      content: "";
      width: 0;
      height: 1px;
      background-color: #042767;
      position: absolute;
      top: 100%;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      -webkit-transition-duration: 0.3s;
              transition-duration: 0.3s; }
    .news-item__link:hover:before {
      width: 100%; }

.country-news {
  padding: 30px 0; }
  .country-news__title {
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 30px; }
  .country-news__filter {
    display: flex;
    margin-bottom: 20px; }
    .country-news__filter .chips {
      margin-right: 15px; }
  .country-news__item {
    margin-bottom: 10px; }

.chips {
  height: 25px;
  border-radius: 15px;
  border: none;
  display: flex;
  align-items: center;
  padding: 0 15px;
  font-weight: 700;
  color: #538AE4;
  background-color: #dbe8fa;
  cursor: pointer;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s; }
  .chips--active {
    background-color: #315a94;
    color: white; }
  .chips:hover {
    background-color: #cedbef;
    color: #0859de; }
  .chips:active, .chips:focus {
    outline: none; }

.footer {
  padding: 10px 20px;
  color: white;
  background-color: #6c6cff; }

